.top-banner,
.rectangle-banner {
  width: 100vw;
  height: 10vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  box-shadow: 0px 1px 20px rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.39);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
} 
  
  .rectangle-banner {
    box-shadow: 0px 1px 20px rgba(0, 0, 0, 0.2);
    width: 100vw;  /* Full width */
    height: 10vh;
    position: fixed;  /* Fixed position */
    top: 0;  /* Stick to the top */
    left: 0;  /* Stick to the left */
    z-index: 100; 
    /*make it a bit trnsparent */
  }
  
  .logo-div-banner {
  font-family: Arial;
  font-weight: 400;
  font-size: 2vw;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20vh;
  z-index: 101; 

  
  }
  
  .logo-banner{

    width: 12vh;  /* Set the width */ 
    object-fit: scale-down;  /* Resize the image to fit within the box */
    z-index: 101; 

  }
  
  .menu-container-banner {
    display: flex;
    justify-content: space-evenly; /* This will distribute them evenly */
    margin: 0% 10%;
    width: 25%;
    align-items: center;
    z-index: 101; 
    position: absolute;
    left: 2vw;
    top: center;
  }
  
  .feed-banner, .community-banner, .ar-gallery-banner, .about-us-banner {
    font-family: Arial;
    font-weight: 700;
    font-size: 100%;
    align-items: center;
    z-index: 101; 

  }
  .verification-link-div{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: center;
    left: 50%;
    color: rgba(0, 0, 0, 0.548);
    z-index: 101; 

  }
  
  
  
  
  .log-out-rectangle-banner, .sign-up-rectangle-banner, .log-in-rectangle-banner {
    border-radius: 100px;
    width: 5vw;
    height: 3vh;
    position: relative;
    top: center;
    z-index: 101; 
    left: auto; /* Remove the left property */
    right: 10%; /* Use right property instead */
  }
  
  .log-out-rectangle-banner {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(245, 248, 255, 1);
    border: 1px solid rgba(222, 232, 255, 1);
    left: 90vw;
    z-index: 102;
  }
  
  .log-in-rectangle-banner {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(245, 248, 255, 1);
    border: 1px solid rgba(222, 232, 255, 1);
    left: 83%;
    right: 17%; /* Use right property instead */

  }
  
  .sign-up-rectangle-banner {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 87, 255, 1);
    
  }
  
  .log-out-banner {
    font-family: Arial;
    font-weight: 400;
    font-size: 1vw;
    text-align: center;
    font-weight: bold;
    color: rgba(0, 87, 255, 1);
    z-index: 102; 

  }

  .sign-up-link-container{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: center;
    left: 91%;
    z-index: 101; 
  }

  .log-in-link-container{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: center;
    left: 80%;
    z-index: 101; 
  }
  
  .log-in-banner {
    font-family: Arial;
    font-weight: 400;
    font-size: 1vw;
    text-align: center;
    font-weight: bold;
    color: rgba(0, 87, 255, 1);
    z-index: 101; 


  }
  
  .sign-up-banner {
    font-family: Arial;
    font-size: 1vw;
    text-align: center;
    font-weight: bold;
    color:aliceblue;
    z-index: 101; 

  }
  

  
  .verificatin-link{
    font-family: Arial;
    font-size: 1vw;
    text-align: center;
    font-weight: bold;
    color:rgba(50, 157, 250, 0.329);
    z-index: 101; 
    position: absolute;
    top: center;
    left: 50%;
  }
  
  .profile-container{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: center;
    left: 73%;
    z-index: 101; 
    height: 7vh;
    width: 15vw;
  }

  
  .profile-rectangle {
    width: 15vw;
    height: 7vh;
    position: relative;  /* Changed to relative */
    left: 0vh;
    top: 0vh;
  }
  .profile-image-square{
    border-radius: 21px;
    width: 4rem;
    height: 4rem;
    position: relative;
    left: 0.5vw;
    top: 0vh;
  }

  .profile-image {
    position: absolute;
    width: 100%;  /* Makes sure it takes the full width of the parent */
    height: 100%;  /* Makes sure it takes the full height of the parent */
  }
  
  .profile-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;  /* This makes sure the image covers the whole area without stretching */
    border-radius: 21px;  /* Optional, if you want the image to have rounded corners */
  }
  
  .name-text {
    color: #181818;
    text-align: left;
    font: 900 12px "Arial", sans-serif;
    text-transform: uppercase;
    position: absolute;
    left: 5.5vw;  /* Adjust as needed */
    top: 0.5vh;  /* Adjust as needed */
  }
  
  .views-per-month {
    color: #747474;
    text-align: left;
    font: 400 12px "Arial", sans-serif;
    position: absolute;
    left: 5.5vw;  /* Adjust as needed */
    top: 2.5vh;  /* Adjust as needed */
  }
  
  .views-variance {
    color: #02b033;
    text-align: left;
    font: 900 12px "Arial", sans-serif;
    position: absolute;
    left: 12vw;  /* Adjust as needed */
    top: 2.5vh;  /* Adjust as needed */
  }
  
  
  /* Media Query for smaller screens */
  @media only screen and (max-width: 768px) {
    .logo-banner, .feed, .community, .ar-gallery, .log-out-rectangle, .sign-up-rectangle, .log-in-rectangle .log-out, .sign-up, .log-in {
      font-size: 2vw;
    }
  }
  