.create-a-post,
.create-a-post * {
  box-sizing: border-box;
  z-index: 51;

}
.create-a-post {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
padding-top: 12vh;

}
.rectangle-2 {
  background: #ffffff;
  border-radius: 4px;
  width: 80vw;
  height: 15vh;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1), 0px 1px 3px rgba(0, 0, 0, 0.2);
  margin-bottom: 16px;
}

.send-button-box {
  background: #0057ff;
  border-radius: 4px;
  width: 3vw;
  height: 4vh;
  position: relative;
  left: calc(50% - -286px);
  top: 10vh;
  left: 75vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-fbx-button {
  border: 1.5px solid rgba(128, 128, 128, 0.555);  /* Added gray border */
  border-radius: 4px;
  width: 3vw;
  height: 4vh;
  position: relative;
  top: 6vh;
  left: 71vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-icon {
  color: #ffffff;
}


.what-s-on-your-mind {
  color: rgba(0, 0, 0, 0.836);
  text-align: left;
  font: 400 18px "Arial", sans-serif;
  position: relative;
  width: 70vw;
  left: 5vw;
  top: 1vh;
}

.what-s-on-your-mind::placeholder {
  color: rgba(24, 24, 24, 0.45);  /* This will be for the placeholder text */
}


.line {
  border-style: solid;
  border-color: #a1a1a154;
  border-width: 1px 0 0 0;
  width: 70vw;
  height: 0px;
  position: relative;
  left: 5vw;
  top: -3vh;
  transform-origin: 0 0;
  transform: rotate(0deg) scale(1, 1);
}
.share-thoughts {
  color: #181818;
  text-align: left;
  font: 900 14px "Arial", sans-serif;
  text-transform: uppercase;
  position: relative;
  left: 5vw;
  top: -7vh;
}

.select-file{
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 102;
  cursor: inherit;

  }
  
.footer {
  transition: all 1s ease-in-out;
  position: fixed;
  bottom: -100px; /* Initially hidden */
  left: 0;
  right: 0;
  background-color: #333;
  color: white;
  text-align: center;
  padding: 16px;
}

.footer.show {
  bottom: 0; /* Show when the class "show" is added */
}
  
.post-container {
  width: 50%;  /* Adjust the width to your needs */
  margin: 16px auto;  /* Horizontally center the div */
  border: 1px solid #ccc;
  padding: 16px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1), 0px 1px 3px rgba(0, 0, 0, 0.2);

}


.profile-section,
.title-section,
.content-section-img-fbx,
.action-section {
  margin-bottom: 16px;
}


.follow-button{
  position: relative;
  left: 5vw;
  font: 900 14px "Arial", sans-serif;
  color: #1c51ff;
}

.comment-button{
  position: relative;
  left: -2vw;
  font: 900 14px "Arial", sans-serif;
  color: #1c51ff;
}


.profile-section {
  display: flex;
  align-items: center;
  height: 8vh;
  
}

.profile-username-text{
  font-size: 1.5rem;
  font-weight: 600;
  margin-left: 8px;
  color: #0057ff;
  text-transform: capitalize;
}

.post-profile-pic{
  width: 5vh;
  height: 5vh;
  border-radius: 50%;
}

.title-section{
  font-size: 1rem;
  font-weight: 600;
  color: #0000009c;
  text-transform: capitalize;
}

.content-section-img-fbx{
  width: 100%;
  height: 100%;
  border-radius: 4px;
}



.like-button{
  margin: 0 auto;

}


