.content-section-3d {
  height: auto;
  width: 30vw;
  height: 30vh;
  margin: 0 auto;
  border-radius: 4px;
  position: relative; /* Make sure the height adjusts automatically */
}

.content-section-img {
  width: 30%;
  margin: 0 auto;
  border-radius: 4px;
  position: relative;
}

canvas {
  border: 1px solid #d3d3d3;
  border-radius: 4px;
  position: relative;  /* Position within the .content-section-img */
  object-fit: cover;
  cursor:grab;
}

.scene-container {
  padding-bottom: 10vh;
  top: 100vh;
  position: absolute;
  width: 100%;
  display: flex;             /* Enable Flexbox */
  justify-content: center;   /* Center horizontally */
  align-items: center;       /* Center vertically */
}

.scene-3d {
  width: 60vw;  /* width as a percentage of viewport width */
  height: 45vh; /* height as a percentage of viewport height */
  position: relative;
  border-radius: 4px;
  border: 1px solid #d3d3d3;
  z-index: 50;
}


