.log-in-sign-up,
.log-in-sign-up * {
  box-sizing: border-box;
}
.log-in-sign-up {
  background: var(--white, #ffffff);
  border-radius: 50px;
  width: 60vw;
  height: 80vh;
  position: relative;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  transform-origin: 0 0;
  transform: rotate(0deg) scale(1, 1);
  overflow: hidden;
  top: 15vh;
  left:  20vw;; 
}
.login-image {
  width: 30vw;
  height: 80vh;
  object-fit: cover;
  object-position: 20% 0;
  position: absolute;
  right:0;
  top: 0;
}
.log-in-container-box {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  left: 2vw;
  top: 2vh;
}

.sign-up-container-box{
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  left: 2vw;
  top: 0vh;
}

.log-in {
  text-align: left;
  width: 20vw;
  font: 700 54px "Montserrat", sans-serif;
  position: absolute;
  left: 0;
  top: 5vh;
}
.log-in-span {
  color: var(--black, #000000);
  font: 700 54px "Montserrat", sans-serif;
}
.log-in-span2 {
  color: #0057ff;
  font: 700 54px "Montserrat", sans-serif;
}
.log-in-subtext {
  color: #000000;
  text-align: left;
  font: 600 14px/24px "Montserrat", sans-serif;
  position: absolute;
  left: 0px;
  top: 13vh;
  width: 20vw;
}
.username-password-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: flex-start;
  justify-content: flex-start;
  position: absolute;
  left: 0px;
  top: 205px;
}
.username-password-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  left: 0;
  top: 20vh;
  width: 25vw;
}

.first-name-text-field, .last-name-text-field, .username-text-field, .password-text-field, .confirm-password-text-field, .email-text-field {
  border: 1px solid var(--gray-5, #e0e0e0);
  padding: 12px 20px;
  width: 100%;
  height: 50px;
}

.username, .password {
  width: 100%;
  font: 400 14px/24px "Montserrat", sans-serif;
  color: #000000;
}


.login-text {
  font: 700 16px "Montserrat", sans-serif;
  text-transform: uppercase;
}

.login-button, .sign-up-button {
  background: #0057ff;
  padding: 12px 24px 12px 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
}


.login-text {
  color: var(--white, #ffffff);
  text-align: left;
  font: 700 16px "Montserrat", sans-serif;
  text-transform: uppercase;
  position: relative;
}

.sigup-container{
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  left: 0;
  top: 18vh;
  width: 25vw
}



.sign-up-button-disabled {
  padding: 12px 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: grey;
  cursor: not-allowed;
  width: 100%;  /* Adjust as needed */
}



.loader {
  animation: spin 1s linear infinite;
  width: 3vw;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.password-do-not-match-text{
  color: red;
  font: 400 14px "Montserrat", sans-serif;
  position: inherit;
  text-align: center;
  align-items: flex-start;
  justify-content: flex-start;
}

.have-an-account-container{
  text-align: left;
  font: 400 14px "Montserrat", sans-serif;
  position: relative;
  top: 21vh;

}

.click-here-text{
  color: #0057ff;
  text-decoration: underline;
  cursor: pointer;
}
