
.meet-the-team-container {
    position: relative;
    top: 15vh;
    text-align: center;
    z-index: 1;
  }
  
  .meet-the-team-text {
    margin-bottom: 15px;
    font: 400 3vw "Bell MT", sans-serif;
    
  }
  
  .meet-the-team-subtext {
    font: 400 1.5vw "Bell MT", sans-serif;
    margin-bottom: 20px;
  }
  
  .team-members {
    position: inherit;
    top: 5vh;
    display: flex;  /* Aligns the team members horizontally */
    justify-content: space-around;  /* Evenly space team members */
  }
  
  .team-member-1, .team-member-2, .team-member-3 {
    width: 30%;  /* each takes up roughly 30% of the container */
    font: 400 1.3vw "Bell MT", sans-serif;
  
  }
  
  .team-member-1-image, .team-member-2-image, .team-member-3-image {
    width: 20rem; /* Diameter */
    height: 20rem; /* Diameter */
    border-radius: 50%; /* Makes it circular */
    object-fit: cover; /* Scale the image to cover the circle */
    object-position: center; /* Adjust this to show the desired part of the image */
  }
  
  
  .team-member-1-name, .team-member-2-name, .team-member-3-name {
    font-size: 1.2em;  /* name text size */
    margin-top: 10px;
  }
  
  .team-member-1-role, .team-member-2-role, .team-member-3-role {
    font-size: 1em;  /* role text size */
  }
  
  .project-information-container {
    position: relative;
    top: 25vh;  /* You can adjust this value */
    text-align: center;
    padding: 20px;
    z-index: 1;
  }
  
  .project-information-text {
    margin-bottom: 20px;
    font: 400 2.5vw "Bell MT", sans-serif;
  }
  
  .project-title-text {
    font-size: 3em;  /* title text size */
  }
  .project-subtitle-text{
    position: relative;
    top: 5vh;
    font: 400 3vw "Bell MT", sans-serif;
  }
  .project-information-subtext {
    width: 85vw;
    margin: 0 auto;
    font: 400 1.5vw "Bell MT", sans-serif;
    margin-bottom: 20px;
    text-align: justify;  /* Aligns the text to both left and right margins */
height: 20vh;
}

  
  .project-information-subtext p {
    text-align: center;
    margin-bottom: 20px; /* Adjust this value as needed */
    position: relative;
    top: 10vh;
    height: 20vh;

  }

  .logo-big-div-banner {
    font-family: Arial;
    font-weight: 400;
    font-size: 2vw;
    position: relative;
    left: 20vw;
    top: 32vh;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50vw;
  
    
    }
    
    .logo-big-banner{
  
      width: 100vw;  /* Set the width */ 
      object-fit: scale-down;  /* Resize the image to fit within the box */
        z-index: -1;
    }