body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.desktop-lm-colour-spread,
.desktop-lm-colour-spread * {
  box-sizing: border-box;
}
.desktop-lm-colour-spread {
  width: 100vw;
  height: 140vh;
  position: relative;
  transform-origin: 0 0;
  transform: rotate(0deg) scale(1, 1);
  overflow-x: initial;
}

.image-1 {
  max-width: 40vw;
  max-height: 100%;
  position: absolute;
  left: 53vw;
  top: 12vh;
  transform-origin: 0 0;
  transform: rotate(0deg) scale(1, 1);
}
.canvas-is-never-your-limit {
  color: #000000;
  text-align: left;
  font: 400 4vw "Bell MT", sans-serif;
  position: absolute;
  left: 7vw;
  top: 24vh;
  transform-origin: 0 0;
  transform: rotate(0deg) scale(1, 1);
}

.exhibition-button {
    background: #0057ff;
    border-radius: 0.7vw;
    width: 8vw;  /* 174/1151 * 100 */
    height: 4vh;  /* 43/604 * 100 */
