/* 
Scene Menu
*/

.menu-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0.1vh;
    left: 40vw;
    width: 20vw;
    height: 82%;
    background: rgba(128, 128, 128, 0.904); /* grey with transparency */
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 51;
    border-radius: 5px;
  }

  .menu-screen h1,
.menu-screen p {
  margin: 8px; /* Adding some margin to space out the elements */
  color: rgb(5, 5, 5); /* Text color */
  font-size: 1.2em; /* Increasing the font size a bit */
}