.background-image {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100vw;  /* 100% of viewport width */
  height: 100vh;
  position: fixed;
  z-index: -1;
  object-fit: cover; 


}
.background-image::before {
  content: "";
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  width: 100%;
  height: 100%;
}