.scene-selector-container{
  position: absolute;
  left: 0vw;
  top: 120vh;
  width: 100%;
  height: 100%;
  transform-origin: 0 0;
  transform: rotate(0deg) scale(1, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;

}
.scene-selector {
  text-align: center;
}

.scene-selector-text {
font: 400 2.5vw "Bell MT", sans-serif;
    text-align: center;
  position: relative;
  top: 15vh;
  left: 3vw;
  margin-right: 5vw;
  display: flex;
  justify-content: center;
  display: flex;
  flex-direction: row;
  align-items: center; /* Adds some space between the text and the first image */
}

.scene-selector-image {
  width: 100vw;  /* Set to your desired width */
  height: 19vh; /* Set to your desired height */
  object-fit: cover;
  margin-right: 5vw; /* Adds some space between the images */
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 18vh;
}

.scene-image {
  width: 120vw; /* or whatever size you want */
  object-fit: cover;
  margin: 0 0.5vw; /* Adds some space between images */
  border-radius: 20px;

}

